<template>
	<div class="wrapper">	
	<h3>-DUJU</h3>	
	<div id="login"> 
        <form id="log" method="" v-show="showLogin" autocomplete="off" >  <span></span> 
        	 <p v-show="showTishi">{{tishi}}</p>
            <input type="text" required="required" placeholder="" v-model="username">  
            <input type="password" required="required" placeholder="" v-model="password">
            <input id="deng" type="button" @click="login" value="" >  
            <input id="zhuce" type="button" @click="ToRegister" value="" > 
        </form> 
        <form id="reg" method="" v-show="showRegister" autocomplete="off" style="display: none">   <span></span> 
            <p v-show="showTishi">{{tishi}}</p>
            <input type="text" required="required" placeholder="" v-model="newUsername">  
            <input type="password" required="required" placeholder="" v-model="newPassword">
            <input type="password" required="required" placeholder="" v-model="ag_password">
            <input id="deng" type="button" @click="ToLogin" value="" >  
            <input id="zhuce" type="button" @click="register" value="" >  
        </form>  
    </div> 
		
	<ul class="bg-bubbles">
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
	</ul>
</div>
</template>

<style>
	@import url(../../assets/css/login.css);
</style>

<script>
import {setCookie,getCookie} from '../../assets/js/cookie.js'
import axios from 'axios'
export default{
    data(){
        return{
            showLogin: true,
            showRegister: false,
            showTishi:false,
            tishi:'',
            username: '',
            password: '',
            newUsername: '',
            newPassword: '',
            ag_password: ''
        }
    },
  mounted(){
  /*cookieusernamecookie*/
    if(getCookie('username')){
        this.$router.push('/index')
    }
  },
  methods:{
  	ToRegister(){
	    this.showRegister = true
	    this.showLogin = false
	  },
  	ToLogin(){
  	    this.showRegister = false
  	    this.showLogin = true
  	},
    login(){
      var self = this; //
        if(self.username == "" || self.password == ""){
            alert("")
        }
        else{
            /**/
            axios({
             method: 'post',
             url: 'api/login',
             data: {
              user_name:self.username,
              user_pwd:self.password
             },
             timeout: 3000
            }).then(function(res) {
               if(res.data == -1){
                  self.tishi = ""
                  self.showTishi = true
              }else if(res.data == 'admin'){
                  self.tishi = ""
                  self.showTishi = true
              }else{
                  self.tishi = ""
                  self.showTishi = true
                  setCookie('username',self.username,1000*60)
                  setTimeout(function(){
                      self.$router.push('/index')
                  }.bind(self),500)
              }
            }).catch(function(error) {
                console.log(error);
                self.tishi = '!'
                self.showTishi = true
            });
        }
    },
    register(){
      var self = this; //
	    if(self.newUsername == "" || self.newPassword == ""){
	        alert("");
	    }
      else if(self.newPassword != self.ag_password){
          alert("");
          self.newPassword = ''
          self.ag_password = ''
      }
      else{
          axios({
             method: 'post',
             url: 'api/register',
             data: {
              user_name:self.newUsername,
              user_pwd:self.newPassword
             },
             timeout: 3000
            }).then(function(res) {
               if(res.data == -1){
                  self.tishi = ""
                  self.showTishi = true
              }else if(res.data == 'admin'){
              /*self.$router.push*/
                  self.tishi = ""
                  self.showTishi = true
              }else{
                  self.tishi = ""
                  self.showTishi = true
                  setTimeout(function(){
                      self.showRegister = false
                      self.showLogin = true
                      self.showTishi = false
                      self.username=self.newUsername
                  }.bind(self),1000)
              }
             }).catch(function(error) {
                  console.log(error);
                  self.tishi = '!'
                  self.showTishi = true
              });
	    }
	  },
  },
}
</script>


