<template>
	<div class="wrapper">	
	<h3>-DUJU</h3>	
	<div id="login"> 
        <form id="log" method="" v-show="showLogin" autocomplete="off" >  <span></span> 
        	 <p v-show="showTishi">{{tishi}}</p>
            <input type="text" required="required" placeholder="" v-model="username">  
            <input type="password" required="required" placeholder="" v-model="password">
            <input id="deng" type="button" @click="login" value="" style="width:60%;margin:5% 20%" >  
        </form>  
    </div> 
		
	<ul class="bg-bubbles">
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
		<li></li>
	</ul>
</div>
</template>

<style>
	@import url(../../assets/css/login.css);
</style>

<script>
import {setCookie,getCookie} from '../../assets/js/cookie.js'
//import axios from 'axios'
export default{
    data(){
        return{
            showLogin: true,
            showRegister: false,
            showTishi:false,
            tishi:'',
            username: '',
            password: '',
        }
    },
  mounted(){
  /*cookieusernamecookie*/
    if(getCookie('username')){
        this.$router.push('/admin-index')
    }else{
    	this.$router.push('/admin-index/login')
    }
  },
  methods:{
    login(){
      var self = this; //
        if(self.username == "" || self.password == ""){
            alert("")
        }else{
            /**/
            self.$axios({
             method: 'post',
             url: '../api/login-ad',
             data: {
              admin_name:self.username,
              admin_pwd:self.password
             },
             timeout: 3000
            }).then(function(res) {
               if(res.data == -1){
                  self.tishi = ""
                  self.showTishi = true
               }else{
                  self.tishi = ""
                  self.showTishi = true
                  setCookie('username',self.username,1000*60)
                  setTimeout(function(){
                      self.$router.push('/admin-index')
                  }.bind(self),1000)
               }
            }).catch(function(error) {
                  console.log(error);
                  self.tishi = '!'
                  self.showTishi = true
              });
        }
    }
  },
}
</script>


